* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
}

html {
  font-family: 'Montserrat', sans-serif;
}

.page-wrapper {
  width: 100%;
  height: 100%;
  padding: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heading {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.timer {
  font-size: 6rem;
  font-weight: bold;
  color: #2980b9;
  margin-bottom: 2rem;
}

.info {
  font-size: 2rem;
  color: #bdc3c7;
}
